    SEND_MAPPINGS: "\n\t\tmutation createMapping(\n\t\t\t$mappings:[MappingInput]\n\t\t\t$typeId: Int\n\t\t) {\n\t\t\t\tdata: createMapping(\n\t\t\t\t\t\tmappings: $mappings\n\t\t\t\t\t\ttypeId:$typeId\n\t\t\t\t){\n\t\t\t\t\tok\n\t\t\t}\n\t\t}\n\t",
